.fixed {
  position:fixed;
  top:0;
  left:0;
  right:0;
  margin:0;
  background-color: $w;
  z-index: 999;
  .section-top {
    display: none;
  }
	.top-header {
		height: 35px;
	}
	.header-inside {
		min-height: 60px;
	}
	.main-navigation {
		.nav-primary {
			> li {
				> a {
					padding: 25px 0;
				}
			}
		}
	}
}
.logo-self {
	align-self: center;
}
.site-logo {
	align-self: center;
}
.top-header {
	background-color: #333333;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	height: 50px;
	align-items: center;
	.widget {
		margin-bottom: 0;
	}
}
.info-left {
	padding: 0;
	margin: 0;
	list-style: none;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;

	li {
		color: $w;
		font-size: 18px;
		font-family: $ff;
		margin-right: 35px;
		position: relative;

		a {
			color: $w;
			&:hover {
				text-decoration: none;
				color: $p;
			}
		}
		&:after {
			position: relative;
			content: "|";
			padding-left: 35px;
			top: -2px;
		}

		&:last-child {
			margin-right: 0;
			&:after {
				display: none;
			}
		}
	}
}

.cnss-social-icon {
	&.list-social {
		text-align: right !important;
		li {

			&.cn-fa-icon {
				margin-right: 15px !important;
				position: relative !important;
				display: inline-block;
				&:after {
					position: relative;
					content: "|";
					display: inline-block !important;
					padding-left: 15px;
					top: -2px;
					color: #808080;
				}
				&:last-child {
					margin-right: 0 !important;
					&:after {
						display: none !important;
					}
				}
				a {
					display: inline-block !important;
					background-color: transparent !important;
					padding: 0 !important;
					height: auto !important;
					margin: auto !important;
					&:hover {
						background-color: transparent !important;
					}
				}
			}
		}
	}
}
.header-inside {
	background-color: $w;
	display: -webkit-flex;
	display: -ms-flex;
	display: flex;
	min-height: 110px;
	align-items: center;
}

.main-navigation {
	text-align: right;
	.nav-primary {
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
		// margin-top: 45px;
		li {
			margin-right: 30px;

			&:hover {
				color: $s;
			}

			a {
				padding: 45px 0;
				color: #333333;
				font-family: $ft;
				font-size: 14px;
				text-transform: uppercase;
				&:hover,&:focus,&:active {
					text-decoration: none;
					color: $p;

				}
			}
			&.current-menu-item {
				a {
					color: $p;
				}
			}
			&:last-child {
				margin-right: 0;
			}
		}
		ul {
			background: $w;
			border-top: 3px solid $p;

			li {
				margin-right: 0;
				a {
					padding: 10px 10px;
					text-align: left;
					width: 250px;
				}
			}
		}
	}
}
